import React, { ButtonHTMLAttributes } from 'react';
import '../all.css';

interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  leftIconURL?: string
  rightIconURL?: string
  containerClassName?: string
  childClassName?: string
  outline?: boolean
  loading?: boolean
}

export function Button(props: ButtonProps) {
  return (
    <div className={`relative ${props.containerClassName || ''}`}>
      <button 
        {...props}
        className={`w-full flex items-center justify-center ${props.loading ? 'invisible' : 'visible'} ${props.outline ? 'border border-[1.5px] border-[#13639E] text-[#13639E] py-[5px]' : 'text-white bg-[#13639E] py-[6px]'} rounded-[6px] px-[12px] gap-[4px] ${props.className || ''}`}>
        { props.leftIconURL && <img 
          className={`w-[15px] h-[15px] object-contain`}
          src={props.leftIconURL} /> }
        <div className={`text-[13px] font-bold flex-1 ${props.childClassName || ''}`}>
          { props.children }
        </div>
        { props.rightIconURL && <img 
          className={`w-[15px] h-[15px] object-contain`}
          src={props.rightIconURL} /> }
      </button>
      { props.loading && <div className={`absolute w-full h-full left-0 top-0 flex items-center justify-center rounded-[6px] bg-zinc-100 shadow-[inset_0px_1px_5px_rgba(0,0,0,.1)]`}>
        <div className='tloading' />
      </div> }
    </div>
  );
}
